/*----------------------------------------------------------------------------------*/
/* Estilos da segunda caixa (que contém as verificações da palavra-passe) 
/* A caixa de mensagem é mostrada quando o utilizador clica no input da password*/
#messageBox {
  color: rgba(3, 3, 56, 0.945);
  padding-top: 10px;
  padding-left: 20px;
  width: 400px;
  height: 200px; 
  margin-top: 30px;
  border-radius: 10px;
  background-color: rgb(255, 255, 255);
  box-shadow: 0 4px 10px 2px rgba(0, 0, 0, 0.4);
}

/* se estiver certa, muda a cor do texto para verde  e adiciona um "✔"*/
.valid {
  color: green;
}

.valid:before {
  position: relative;
  left: 0px;
  content: "✔";
  margin-right: 10px; /* adiciona algum espaçamento entre o ícone e o texto */
}

/* se estiver errada, muda a cor do texto para vermelho e adiciona um "x"*/
.invalid {
  color: red;
}

.invalid:before {
  position: relative;
  left: 0px; /* ajusta conforme necessário */
  content: "✖";
  margin-right: 10px; /* adiciona algum espaçamento entre o ícone e o texto */
}



.erro-campo {
  color: red;
  font-size: small;
}




/*-----------------------------------------------------------------------*/
/*Estilos do pop up personalizado de confirmação da palavra passe

/* The Modal (background) */
.modal {
  visibility:hidden;
  position: fixed; /* Stay in place */
  z-index: 2; /* Sit on top */
  padding-top: 100px; /* Location of the box */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  /*overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.53); /* Black w/ opacity */
  } 

  /* Modal Content */
.modal-content {
  top: 100px;
  background-color: #fefefe;
  margin: auto;
  padding: 20px;
  border: 1px solid #888;
  width: 30%;
  border-radius: 4px;
  border-style: 50px solid;
  box-sizing: 10px;
  border-color: rgb(150, 0, 0);
  text-align: center;
  font-size: large;
  box-sizing: border-box;
  margin-top: 100px;
} 

.letra2{
  font-size: small;
  color: rgb(163, 0, 0);
}




